/**
 * Fonts
*/
@font-face {
  font-family: "HUGE Avant Garde Bold";
  src: url("./../fonts/huge_agb_v5-webfont.eot?#iefix") format("embedded-opentype"), url("./../fonts/huge_agb_v5-webfont.woff") format("woff"), url("./../fonts/huge_agb_v5-webfont.ttf") format("truetype"), url("./../fonts/huge_agb_v5-webfont.svg#svgFontName") format("svg");
}

@font-face {
  font-family: "Galaxie";
  font-style: normal;
  font-weight: normal;
  src: url("./../fonts/GalaxieCopernicus-Book.eot?#iefix") format("embedded-opentype"), url("./../fonts/GalaxieCopernicus-Book.woff") format("woff"), url("./../fonts/GalaxieCopernicus-Book.svg#svgFontName") format("svg");
}

@font-face {
  font-family: "Galaxie";
  font-style: italic;
  font-weight: normal;
  src: url("./../fonts/GalaxieCopernicus-BookItalic.eot?#iefix") format("embedded-opentype"), url("./../fonts/GalaxieCopernicus-BookItalic.woff") format("woff"), url("./../fonts/GalaxieCopernicus-BookItalic.svg#svgFontName") format("svg");
}

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 968px;
  padding-left: 12px;
  padding-right: 12px;
  width: 100%;
}

.container::after {
  clear: both;
  content: "";
  display: table;
}

/**
 * Print Stylesheet
*/
@media print {
  @page {
    margin: 1cm;
  }
  body {
    background: #ffffff;
    color: #000000;
  }
  a {
    page-break-inside: avoid;
  }
  blockquote {
    page-break-inside: avoid;
  }
  table,
  pre {
    page-break-inside: avoid;
  }
  ul,
  ol,
  dl {
    page-break-before: avoid;
  }
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
summary,
main {
  display: block;
}

audio,
canvas,
video {
  display: inline-block;
}

body {
  margin: 0;
}

ul {
  list-style-type: none;
}

a {
  background: transparent;
  text-decoration: none;
}

button,
input {
  line-height: normal;
}

input[type='search'] {
  -webkit-appearance: textfield;
}

input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

textarea:focus,
input:focus {
  outline: 0;
}

input {
  appearance: none;
  margin: 0;
  padding: 0;
}
